import React from "react"

import { StaticQuery, Link, graphql } from "gatsby"

/* import "/home/cabox/workspace/gatsby-starter-weather/src/styles/global.css" */

// import "./src/styles/global.css" 

import "../styles/global.css" 

export default ({ children }) => (
    <StaticQuery
    query={graphql`
      query {
  openWeather {
    id
    internal {
      type
      contentDigest
      owner
    }
    list{
      
    main {
        temp
        temp_min
        temp_max
        pressure
        sea_level
        grnd_level
        humidity
        temp_kf
      }
    }
    
    city {
      id
      name
      coord
      {
        lat
        lon
      }
    }
  }
} `
}
    render={data => (
  <div>
        <Link to="/" style={{ float: `right`}}>
      Home
    </Link>
    {children}
        {/* <Link to="/">Home    </Link>  */}
     <h3>
            <h3>City Name: {data.openWeather.city.name}</h3>
            <h3>City Coord lat: {data.openWeather.city.coord.lat}</h3>
            <h3>City Coord lon: {data.openWeather.city.coord.lon}</h3>
     </h3> 
	
  </div>
    )}
  />
    )